import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { DomSanitizer } from '@angular/platform-browser';
import { AngularFirestore } from 'angularfire2/firestore';
import { Livestream, Collections, Note } from '../../app/app.models';
import { tap } from 'rxjs/operators';
import { Observable } from 'rxjs';

@IonicPage({segment: 'asesoria/:id'})
@Component({
  selector: 'page-asesoria-detail',
  templateUrl: 'asesoria-detail.html',
})
export class AsesoriaDetailPage {

  public id: string = this.navParams.get('id')
  public uid: string = localStorage.getItem('uid')
  
  public live$: Observable<Livestream>
  public notas: Note[]
  public chat: any

  public newNota: any = {text: '', title: ''}
  public lc: boolean = false

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private sanitize: DomSanitizer,
    private toast: ToastController,
    private afs: AngularFirestore
  ) {
  }

  ionViewDidLoad() {
    this.loadAsesoria()
  }

  async loadAsesoria() {

    this.live$ = await this.afs.collection(Collections.LIVESTREAM).doc<Livestream>(this.id).valueChanges()
      .pipe(tap(a => this.chat = this.sanitize.bypassSecurityTrustResourceUrl(`https://www.youtube.com/live_chat?v=${this.YouTubeGetID(a.url)}&embed_domain=${document.domain}`)))

  }

  sanitizeUrl(url) {
    return this.sanitize.bypassSecurityTrustResourceUrl(`https://youtube.com/embed/${this.YouTubeGetID(url)}`)
  }

  YouTubeGetID(url){
    var ID = '';
    url = url.replace(/(>|<)/gi,'').split(/(vi\/|v=|\/v\/|youtu\.be\/|\/embed\/)/);
    if(url[2] !== undefined) {
      ID = url[2].split(/[^0-9a-z_\-]/i);
      ID = ID[0];
    }
    else {
      ID = url;
    }
      return ID;
  }

  addNota(title: string, text: string) {

    this.lc = true
    this.back.addNota(title, text, 'asesoria', this.id).subscribe(data => {
      /* this.a.notas.push(data) */
      this.toast.create({message: 'Nota agregada correctamente.', duration: 2000}).present()
      this.lc = false
      this.newNota = {text: '', title: ''}
    }, err => {
      this.lc = false
      this.toast.create({message: 'No se pudo agregar tu nota...', duration: 2000}).present()
    })

  }

}



